import React, { useState, useEffect } from 'react';
import axios from "axios";
import { Redirect, useParams, Link } from "react-router-dom";
import { removeItem } from "../actions/cart";
import { useDispatch, useSelector } from "react-redux";

import HeaderTop from '../components/HeaderComponents/HeaderTop';
// import CategoryProducts from "../components/productsComponents/CategoryProduct";
import Footer from '../components/Footer';
import Contact from '../components/contact';
import Searchpopup from "../components/searchPopup";
import Paymentbox from '../components/DashboardComponents/paymentBox';
import Paymentblock from '../components/paymentBlock';

const Payment = ({ history }) => {
    const dispatch = useDispatch();
    const { user } = useSelector(state => state.user);
    const { id } = useParams();

    const [count, setCount] = useState(1);
    const [showContact, setShowContact] = useState(false);
    const [showSearch, setShowSearch] = useState(false);
    const [value, setValue] = useState("");
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");
    const [render, setRender] = useState(false);
    const [totalPrice, setTotalPrice] = useState(0);
    const [all, setAll] = useState(0);

    let arr = [];
    const getTotal = (val) => {
        // console.log(val);
        arr.push(val);
        // console.log(arr);
        // setTotalPrice(val);
        setTimeout(() => {
            // arr.push(totalPrice);
            // console.log(arr, totalPrice);
            const _total = arr.reduce((a, b) => b + a);
            console.log(_total);
            setTotalPrice(_total);
        }, 1000)
    }

    useEffect(() => {
        setLoading(true);
        // console.log(user._id);
        const fetchData = async () => {
            let token = JSON.parse(localStorage.getItem("token"));
            try {
                const res = await axios.get(`${process.env.REACT_APP_GET_CART}/${id}`, {
                    headers: {
                        "authorization": `bearer ${token}`
                    }
                });
                // console.log(res.data);
                setData(res.data.data);
                // console.log(data);
                setLoading(false);
            } catch (err) {
                setLoading(false);
                console.log(`error is ${err}`);
            }
        }

        const timer = setTimeout(() => {
            fetchData();
            // setContent(data.product.id);
            // if (data.product.id !== undefined) {
            //     setContent(data.product.id);
            // }
        }, 50);

        return () => {
            clearTimeout(timer);
        }
    }, [render]);

    if (user.name === undefined) return <Redirect to="/login" />

    const getValue = (data) => {
        setValue(data);
    }

    const onShowContact = () => setShowContact(!showContact);
    const onShowSearchBox = () => setShowSearch(!showSearch);
    console.log(totalPrice);

    let total = 0;
    // console.log(data);
    // if (data.product.id !== undefined) {
    //     setContent(data.product.id);
    // }

    return (
        <div className="container">
            {showContact && <Contact onContact={onShowContact} />}
            {showSearch && <Searchpopup onSearch={onShowSearchBox} searchValue={value} />}
            <HeaderTop
                showContact={onShowContact}
                showSearch={onShowSearchBox}
                parentCallback={getValue} />
            <div className="payment">
                <div className="payment-cart">
                    <h2 className="hd-text">ShoppingCart.</h2>
                    <div className="payment-table">
                        <div className="payment-hdtable">
                            <ul>
                                <li className="payment-list hd">
                                    <span>product</span>
                                    <span>quantity</span>
                                    <span>total price</span>
                                </li>
                            </ul>
                        </div>
                        <div className="payment-items">
                            <ul>
                                {data.length !== 0 ? <>
                                    <Paymentbox
                                        id={data._id}
                                        name={data.product.name}
                                        price={data.product.price}
                                        quan={data.quantity}
                                        category={data.product.category}
                                        photo={data.product.imageCover}
                                        totalCallback={getTotal} />
                                </> : <h2>loading</h2>}
                                {/* {data.map((product, i) => <> */}

                                {/* </>)} */}
                            </ul>
                        </div>
                    </div>
                    <div className="payment-bottom">
                        <div className="payment-back">
                            <button onClick={() => history.goBack()}>back</button>
                        </div>
                        <div className="payment-ledger">
                            <span className="payment-subtotal">subtotal: ${totalPrice}</span>
                            <span className="payment-shipping">shipping: free</span>
                            <span className="payment-total">total: ${totalPrice}</span>
                        </div>
                    </div>
                    <Link to={data.product && `/buy/${data.product.id}/${data.quantity}`} className="bt-block">continue</Link>
                </div>

                <div className="payment-block">
                    <Paymentblock />
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default Payment;
