const Dashboard = () => {
    const { user } = useSelector(state => state.user);
    // const dispatch = useDispatch();
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");

    const completedStatus = orders.filter((el) => el.status === "completed");
    const _complete = completedStatus.length;

    const { email, name } = user;

    useEffect(() => {
        setLoading(true);
        // console.log(user._id);
        const fetchData = async () => {
            let token = JSON.parse(localStorage.getItem("token"));
            try {
                const res = await axios.get(`${process.env.REACT_APP_ORDER_USER_ROUTE}/${user._id}`, {
                    headers: {
                        "authorization": `bearer ${token}`
                    }
                });
                // console.log(res.data);
                setOrders(res.data.orders);
                // console.log(data);
                setLoading(false);
            } catch (err) {
                setLoading(false);
                console.log(`error is ${err}`);
            }
        }
        const timer = setTimeout(() => {
            fetchData();
        }, 50);

        return () => {
            clearTimeout(timer);
        }

    }, []);
    console.log(orders);
    // if (!user.loggedIn) {
    //     return <Redirect to="/login" />
    // }

    return (
        <MainDash>
            {/* {popup && <NotiPopup content={content} color={color} />} */}
            <main className="dashboard-main">
                <OrderBox username={name} />
                {/* <div className="loading-spinner" /> */}
                <div className="dashHome-order">
                    {loading && <div className="loading-spinner" />}
                    {
                        orders.length === 0 ? <>
                            {!loading && <NoOrder />}
                        </>
                            : <>
                                <div className="dashHome-table">
                                    <div className="dashHome-order_block">
                                        <ul>
                                            <li className="dashHome-list hd">
                                                <span>date-time</span>
                                                <span>order items</span>
                                                <span>amount</span>
                                                <span>delivery type</span>
                                                <span style={{ marginLeft: "auto" }}>status</span>
                                            </li>
                                        </ul>
                                        {
                                            orders.map((el, i) => (
                                                <ul>
                                                    <li className="dashHome-list" key={i}>
                                                        <span>
                                                            <span className="dashHome-des" style={{ marginRight: "auto" }}>time</span>
                                                            {/* {el.date.split(" ")[0]}-{el.date.split(" ")[4]}&nbsp;{el.date.split(" ")[5]} */}
                                                            {el.date.split("T")[0]}
                                                        </span>
                                                        <span>
                                                            <span className="dashHome-des" style={{ marginRight: "auto" }}>order -items</span>
                                                            {el.product[0].name}  {el.product.length === 1 ? "" : ` +${el.items.length - 1} more`}
                                                        </span>
                                                        <span>
                                                            <span className="dashHome-des" style={{ marginRight: "auto" }}>amount</span>
                                                            NGN {el.amount}
                                                        </span>
                                                        <span style={{ fontSize: "" }}>
                                                            <span className="dashHome-des" style={{ marginRight: "auto" }}>delivery type</span>
                                                            {el.delivery_type}
                                                        </span>
                                                        <span>
                                                            <span className="dashHome-des">status</span>
                                                            <span className={el.status} style={{ marginLeft: "auto" }}>
                                                                {el.status}
                                                            </span>
                                                        </span>
                                                    </li>
                                                </ul>
                                            ))
                                        }
                                    </div>
                                </div>
                            </>
                    }

                </div>
            </main>
        </MainDash>
    );
}

export default Dashboard;
