import React, { useState } from 'react';

//components
import Header from "../components/Header";
import Product from "../components/Product";
import Deals from "../components/Deals";
import Footer from '../components/Footer';
import Contact from '../components/contact';
import Searchpopup from "../components/searchPopup";

const Home = () => {
    const [showContact, setShowContact] = useState(false);
    const [showSearch, setShowSearch] = useState(false);
    const [value, setValue] = useState("");

    const getValue = (data) => {
        setValue(data);
    }

    const onShowContact = () => setShowContact(!showContact);
    const onShowSearchBox = () => setShowSearch(!showSearch);
    console.log(value);

    return (
        <div className={"container"}>
            {showContact && <Contact onContact={onShowContact} />}
            {showSearch && <Searchpopup onSearch={onShowSearchBox} searchValue={value} />}
            <Header onContact={onShowContact} onSearch={onShowSearchBox} paCallback={getValue} />
            <Product />
            <Deals />
            <Footer />
        </div>
    );
}

export default Home;
