import React, { useState, useEffect } from 'react';
import axios from "axios";
import Whirligig from 'react-whirligig';
import { ArrowLeft, ArrowRight } from "@material-ui/icons";

import ProductCard from "./productCard";

const FeaturedProduct = () => {
    const [productsLength, setProductsLength] = useState(0);
    const [cheapProducts, setCheapProducts] = useState([]);
    const [width, setWidth] = useState(5);

    let whirligig;
    const next = () => whirligig.next();
    const prev = () => whirligig.prev();

    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_GET_STORE}/top-five-cheap`);
                setCheapProducts(res.data.data.products);
            } catch (err) {
                console.log(`err is ${err}`);
            }
        }
        const timer = setTimeout(() => {
            fetchData();
        }, 500);

        if (document.body.clientWidth >= 921 && document.body.clientWidth < 1110) {
            //phone size
            setWidth(4);
        } else if (document.body.clientWidth >= 626 && document.body.clientWidth < 920) {
            setWidth(3);
        } else if (document.body.clientWidth >= 436 && document.body.clientWidth < 625) {
            setWidth(2);
        } else if (document.body.clientWidth < 435) {
            setWidth(1);
        }

        return () => {
            clearTimeout(timer);
        }
    }, [width]);

    return <>
        <div className="product-heading">
            <h2 className={"product-heading_text"}>featured products</h2>
            <button onClick={prev} className={"product-heading_btn"}>
                <ArrowLeft className={"product-heading_icon"} />
            </button>
            <button onClick={next} className={"product-heading_btn"}>
                <ArrowRight className={"product-heading_icon"} />
            </button>
        </div>

        <div className="product-cards">
            {/* the slider functionalty is caused by react-whirligig */}
            <Whirligig visibleSlides={width} gutter={"1em"} preventScroll={false}
                ref={(_whirligigInstance) => { whirligig = _whirligigInstance }}
                style={{ padding: "2rem 0" }}>
                {React.Children.toArray(
                    cheapProducts.map((el, i) => (
                        <ProductCard id={el._id} name={el.name} key={i} price={el.price}
                            rating={el.ratingsAverage} text={el.description} photo={el.imageCover}
                        />
                    ))
                )}
            </Whirligig>
        </div>
    </>
}

export default FeaturedProduct;

