import React, { useState } from "react";
import { NavLink, Link } from "react-router-dom";
import { useSelector } from "react-redux";

import Respanel from "./ResPanel";

//icons
import { Search, Call, Close } from "@material-ui/icons";
import {
    AiOutlineShoppingCart
} from "react-icons/ai";
import { HiOutlineMenuAlt2 } from "react-icons/hi";

const HeaderTop = ({ showContact, parentCallback, showSearch }) => {
    const [searchValue, setSearchValue] = useState("");
    const { loggedIn } = useSelector(state => state.user);
    const [openPanel, setOpenPanel] = useState(false);

    const onSubmit = (e) => {
        e.preventDefault();
        parentCallback(searchValue);
    }

    if (openPanel) {
        document.body.classList.add("over-hidden");
    } else {
        document.body.classList.remove("over-hidden");
    }

    const togglePanel = () =>
        setOpenPanel(!openPanel);

    return (
        <div className="header-top">
            <div className="header-icon">
                <h2 className="header-icon_txt">
                    ruby-stores
                </h2>
                <div className="header-behanze">&nbsp;</div>
            </div>
            <form className={"header-form"} onSubmit={(e) => onSubmit(e)}>
                <input type="search" className={"header-form_input"}
                    placeholder={"search products & brands"}
                    onChange={(e) => setSearchValue(e.target.value)}
                />
                <button className={"header-form_button"} onClick={showSearch}>
                    <Search className={"header-form_icon"} />
                </button>
            </form>
            <nav className="res-nav">
                <ul>
                    <li>
                        <Link to="/searchpanel" activeclassname="res-icon_active">
                            <Search className="res-icon" />
                        </Link>
                    </li>
                    {
                        loggedIn && <li>
                            <NavLink to={loggedIn ? "/dashboard/cart" : "/login"} activeclassname="res-icon_active">
                                <AiOutlineShoppingCart className="res-icon" />
                            </NavLink>
                        </li>
                    }

                    <li>
                        <Link to="#" onClick={togglePanel} className="res-icon_button">
                            {
                                openPanel ? <Close className="res-icon" onClick={togglePanel} />
                                    : <HiOutlineMenuAlt2 className="res-icon" onClick={togglePanel} />
                            }
                        </Link>
                    </li>
                </ul>
            </nav>

            {openPanel && <Respanel onClose={togglePanel} />}
            <nav className="header-nav">
                <div className="header-nav_div">
                    <Link to={"#"} className="header-nav-text" onClick={showContact}>contact us</Link>
                </div>
                <div className="header-nav_div">
                    {loggedIn ? <>
                        <Link to="/dashboard/cart" className="header-nav_cart">
                            <span className="header-nav_iconSpan">
                                <AiOutlineShoppingCart className="header-nav_icon" />
                            </span>
                            <span>
                                Cart
                            </span>
                        </Link>
                    </> :
                        <>
                            <h4 className="header-nav-box">
                                <Link to={"/signup"} className="header-nav-text">sign up</Link>
                                <span style={{ fontSize: '19px' }}> | </span>
                                <Link to={"/login"} className="header-nav-text">login </Link>
                            </h4>
                        </>}

                </div>
            </nav>
        </div>
    );
}

export default HeaderTop;
