import React, { useEffect, useState } from 'react';
import axios from "axios";
import Whirligig from 'react-whirligig';
import { ArrowLeft, ArrowRight } from "@material-ui/icons";

import DealCard from "./DealsComponents/DealCard";
import Clock from "./DealsComponents/Clock";

const Deals = () => {
    const [data, setData] = useState([]);
    const [width, setWidth] = useState(4);

    useEffect(() => {
        const url = `${process.env.REACT_APP_GET_STORE}/getPopularProducts`;

        const fetchData = async () => {
            try {
                const res = await axios.get(url);
                // const json = await response.json();
                // console.log(json.data);
                setData(res.data.data.products);
            } catch (err) {
                console.log("error", err);
            }
        }

        fetchData();

        if (document.body.clientWidth >= 701 && document.body.clientWidth < 875) {
            //phone size
            setWidth(3);
        } else if (document.body.clientWidth >= 470 && document.body.clientWidth < 700) {
            setWidth(2);
        } else if (document.body.clientWidth >= 300 && document.body.clientWidth < 469) {
            setWidth(1);
        }
    }, [width]);

    let whirligig;
    const next = () => whirligig.next();
    const prev = () => whirligig.prev();

    return (
        <section className={"deals"}>
            <div className="deals-box">
                <div className="deals-text">
                    <h2 className="deals-h2">
                        deals of the day
                    </h2>
                    <div className="deals-clock">
                        <Clock />
                    </div>
                </div>
                <div className="deals-card">
                    <button onClick={prev} className={"deals-heading_btn"}>
                        <ArrowLeft className={"deals-heading_icon"} />
                    </button>
                    <Whirligig visibleSlides={width} gutter={".5px"} preventScroll={false}
                        ref={(_whirligigInstance) => { whirligig = _whirligigInstance }}
                        style={{ padding: "1rem 0", width: "58%" }}>
                        {React.Children.toArray(
                            data.map((el, i) => (
                                <DealCard name={el.name} key={i} price={el.price}
                                    rating={el.ratingsAverage} text={el.description} id={el._id}
                                    photo={el.imageCover}
                                />
                            ))
                        )}
                    </Whirligig>
                    <button onClick={next} className={"deals-heading_btn"}>
                        <ArrowRight className={"deals-heading_icon"} />
                    </button>
                </div>
            </div>
        </section>
    );
}

export default Deals;