import React, { Component } from 'react';
import { BrowserRouter, Route } from "react-router-dom";

//pages
import Home from "../pages/home";
import Login from "../pages/Login";
import SignUp from "../pages/signup";
import ProductDesc from "../pages/ProductDesc";
import Payment from "../pages/payment";
import Dashboard from "../pages/Dashboard";
import Cart from "../pages/dashboard/cart";
import Profile from "../pages/dashboard/profile";
import Settings from "../pages/dashboard/settings";
import Buy from '../pages/buy';
import SearchPanel from '../pages/SearchPanel';
import Contact from '../pages/contact';

import ProtectedRoute from "../components/utils/ProtectedRoute";
class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <Route exact path={"/"} component={Home} />
                <Route exact path={"/login"} component={Login} />
                <Route exact path={"/signup"} component={SignUp} />
                <Route exact path={"/searchpanel"} component={SearchPanel} />
                <Route exact path={"/contact"} component={Contact} />
                <Route exact path={"/productDesc/:id"} component={ProductDesc} />
                <ProtectedRoute exact path={"/payment/:id"} component={Payment} />
                <ProtectedRoute exact path={"/buy/:id/:quan"} component={Buy} />
                <ProtectedRoute exact path={"/dashboard/home"} component={Dashboard} />
                <ProtectedRoute exact path={"/dashboard/cart"} component={Cart} />
                <ProtectedRoute exact path={"/dashboard/profile"} component={Profile} />
                <ProtectedRoute exact path={"/dashboard/setting"} component={Settings} />
            </BrowserRouter>
        );
    }
}

export default App;