import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import escapeRegexp from "escape-string-regexp";
import axios from "axios";

import SearchCard from "./searchCard";

import photo1 from "../utils/img/tour-1-1.jpg";

const Searchpopup = ({ onSearch, searchValue }) => {
    const [products, setProducts] = useState([]);
    let showingProduct;

    useEffect(() => {
        const fetchProduct = async () => {
            try {
                const res = await axios.get(process.env.REACT_APP_GET_STORE);
                setProducts(res.data.data.products);
            } catch (err) {
                console.log(`error is ${err.message}`)
            }
        }
        const timer = setTimeout(() => {
            fetchProduct();
        }, 500);
        return () => {
            clearTimeout(timer);
        }
    }, []);

    if (searchValue.length > 3) {
        const match = new RegExp(escapeRegexp(searchValue), 'i');
        showingProduct = products.filter((product) => match.test(product.name));
    } else {
        showingProduct = [];
    }

    console.log(showingProduct);

    return (
        <main className="search search-grid" style={{ zIndex: "2" }}>
            <div className="search-box fx" style={{ zIndex: "0", height: "70vh" }}>
                <h4 className="contact_form-text">Search Result</h4>
                {/* <a href="/" className="auth-close">&times;</a> */}
                <Link to="#" onClick={onSearch} className="search-close">&times;</Link>
            </div>
            <aside className="search-block" style={{ zIndex: "20" }}>
                <ul className="search-list">
                    {showingProduct.length !== 0 ?
                        <>
                            {showingProduct.map((prod, i) => (
                                <SearchCard
                                    name={prod.name}
                                    photo={prod.imageCover}
                                    id={prod.id}
                                    price={prod.price}
                                    category={prod.category}
                                    /> 
                            ))}
                        </>
                        : <div>
                            <h3 className="search-valid">
                                make sure the keyword is more than 3...
                            </h3>
                        </div>}
                    {/* <SearchCard
                        name={"iphone 18"}
                        photo={photo1}
                        id={"64773"}
                        price={"5677"}
                        category={"apple phones"}
                    /> */}

                </ul>
            </aside>
        </main>
    );
}

export default Searchpopup;
