import React, { useEffect, useState } from 'react';
import { Link, Redirect } from "react-router-dom";
import axios from "axios";
import { removeItem } from "../../actions/cart";
import { useDispatch, useSelector } from "react-redux";

import MainDash from '../../components/DashboardComponents/MainDash';
import Noitem from '../../components/DashboardComponents/NoItem';
import NotiPopup from '../../components/utils/NotiPopup';
//icons
import { AiOutlineDelete } from "react-icons/ai";
//photo
import photo1 from "../../utils/img/tour-1-1.jpg";


const Cart = () => {
    const dispatch = useDispatch();
    const { user } = useSelector(state => state.user);
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");
    const [render, setRender] = useState(false);

    useEffect(() => {
        setLoading(true);
        // console.log(user._id);
        const fetchData = async () => {
            let token = JSON.parse(localStorage.getItem("token"));
            try {
                const res = await axios.get(`${process.env.REACT_APP_GET_ALL_USER_CART}/${user._id}`, {
                    headers: {
                        "authorization": `bearer ${token}`
                    }
                });
                // console.log(res.data);
                setData(res.data.data);
                // console.log(data);
                setLoading(false);
            } catch (err) {
                setLoading(false);
                console.log(`error is ${err}`);
            }
        }
        const timer = setTimeout(() => {
            fetchData();
        }, 50);

        return () => {
            clearTimeout(timer);
        }

    }, [render]);

    // console.log(data);

    const removeFromCart = (e) => {
        e.preventDefault();
        let { id } = e.target;
        // console.log(e.target.id);
        // console.log(id);
        dispatch(removeItem(id))
            .then(() => {
                setLoading(false);
                setColor("success");
                setContent("item removed from cart");
                setPopup(true);
                setRender(!render);
                // setTimeout(() => {
                //     return <Redirect to="/dashboard/cart"/>
                // }, 2000)
            })
            .catch((err) => {
                setLoading(false);
                setColor("danger");
                setContent("something went wrong!");
                setPopup(true);
            })
    }

    if (popup) {
        setTimeout(() => {
            setPopup(false);
        }, 3000)
    }


    return (
        <MainDash>
            <main className="dashboard-main">
                {loading && <div className="loading-spinner" />}
                {popup && <NotiPopup content={content} color={color} />}
                <div className="dashCart">
                    <div className="dashCart-block">
                        <div className="dashCart-box">
                            {data.length !== 0 ? <>
                                {data.map((product, i) => (
                                    <div className="dashCart-card" key={i}>
                                        <figure className="dashCart-image">
                                            <img src={`${process.env.REACT_APP_HOST_IMG}/${product.product.imageCover}`} className="dashCart-img" />
                                        </figure>
                                        <aside className="dashCart-inner">
                                            <h3 className="dashCart-text">
                                                <span className="dashCart-text-1">{product.product.name}</span>
                                                <span className="dashCart-text-2">${product.product.price}</span>
                                                <span className="dashCart-text-3">quantity: {product.quantity}</span>
                                            </h3>
                                            <div className="buttons">
                                                <Link to={`/payment/${product._id}`} className="buttons-link">buy</Link>
                                                <button
                                                    id={product._id}
                                                    className="buttons-remove"
                                                    onClick={(e) => removeFromCart(e)}
                                                >
                                                    <span>
                                                        <AiOutlineDelete className="buttons-icon" />
                                                    </span>
                                                    remove item
                                                </button>
                                            </div>
                                        </aside>
                                    </div>
                                ))}
                            </> : <div className="dashCart-abs">
                                {/* if there is no item */}
                                {!loading && <Noitem />}
                            </div>}
                        </div>
                    </div>

                </div>
            </main>
        </MainDash>
    );
}

export default Cart;
