import React, { useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { updateMe } from '../../actions/auth';

import MainDash from '../../components/DashboardComponents/MainDash';
import NotiPopup from '../../components/utils/NotiPopup';

const Profile = () => {
    const { user } = useSelector((state) => state.user);
    const dispatch = useDispatch();
    const [name, setName] = useState(user.name);
    const [email, setEmail] = useState(user.email);
    const [phoneNumber, setPhoneNumber] = useState(user.phone_number);
    const [address, setAddress] = useState(user.address);
    const [country, setCountry] = useState(user.country);
    const [emailError, setEmailError] = useState(false);
    const [loading, setLoading] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");

    const onEmailChange = (e) => {
        setEmail(e.target.value);
        const pattern =
            /[a-zA-Z0-9]+[\.]?([a-zA-Z0-9]+)?[\@][a-z]{3,9}[\.][a-z]{2,5}/g;
        if (pattern.test(e.target.value)) {
            e.persist();
            setEmail(e.target.value);
            setEmailError(false);
        } else {
            setEmailError(true);
        }
    };

    const onSubmit = (e) => {
        e.preventDefault();
        setLoading(false);
        if (
            emailError === false
        ) {
            const data = {
                name,
                email,
                phone_number: phoneNumber,
                address,
                country
            }
            console.log(data);
            dispatch(updateMe(data))
                .then(() => {
                    setColor("success");
                    setContent("done!");
                    setPopup(true);
                    window.location.reload();
                })
                .catch(err => {
                    setColor("danger");
                    setContent("failed!");
                    setPopup(true);
                });
            setLoading(false);
        }
    }

    if (popup) {
        setTimeout(() => {
            setPopup(false);
        }, 3000)
    }

    return (
        <MainDash>
            {popup && <NotiPopup content={content} color={color} />}
            <main className="dashboard-main">
                <div className="profile">
                    <h2 className="profile-hdText">profile information</h2>

                    <div className="profile-block">
                        <form className="profile-form">
                            <h3>name
                                <span style={{ color: "red" }}>*</span>
                            </h3>
                            <div className="profile-form_group">
                                <input type="text"
                                    onChange={(e) => setName(e.target.value)}
                                    className="profile-form_input"
                                    placeholder="name"
                                    id="name"
                                    name={"name"}
                                    defaultValue={user ? user.name : ""}
                                    required={true} />
                                {/* <label htmlFor="name" className="signup-form_label">name</label> */}
                            </div>

                            <h3>email
                                <span style={{ color: "red" }}>*</span>
                            </h3>
                            <div className="profile-form_group">
                                <input type="email"
                                    onChange={(e) => onEmailChange(e)}
                                    className="profile-form_input"
                                    placeholder="email"
                                    id="email"
                                    name={"email"}
                                    defaultValue={user ? user.email : ""}
                                    required={true} />
                                {/* <label htmlFor="email" className="signup-form_label">email</label> */}
                                <div className="">
                                    {emailError && <span>please provide a valid bitcoin address</span>}
                                </div>
                            </div>

                            <h3>
                                phone number
                            </h3>
                            <div className="profile-form_group">
                                <input type="number"
                                    onChange={(e) => setPhoneNumber(e.target.value)}
                                    className="profile-form_input"
                                    placeholder="phone number"
                                    id="number"
                                    defaultValue={user ? user.phone_number : ""}
                                    name={"name"}
                                // required={true}
                                />
                                {/* <label htmlFor="number" className="profile-form_label">phone number</label> */}
                            </div>

                            <h3>Residential address
                                <span style={{ color: "red" }}>*</span>
                            </h3>
                            <div className="profile-form_group">
                                <input type="text"
                                    onChange={(e) => setAddress(e.target.value)}
                                    className="profile-form_input"
                                    placeholder="residential address"
                                    id="adddress"
                                    defaultValue={user ? user.address : ""}
                                    name={"address"}
                                // required={true}
                                />
                                {/* <label htmlFor="address" className="profile-form_label">residential address</label> */}
                                {/*  */}
                            </div>

                            <h3>country
                                <span style={{ color: "red" }}>*</span>
                            </h3>
                            <div className="profile-form_group">
                                <input type="text"
                                    onChange={(e) => setCountry(e.target.value)}
                                    className="profile-form_input"
                                    placeholder="country"
                                    id="country"
                                    defaultValue={user ? user.country : ""}
                                    name={"country"}
                                // required={true}
                                />
                                {/* <label htmlFor="country" className="profile-form_label">country</label> */}
                                {/*  */}
                            </div>

                            <button
                                onClick={(e) => onSubmit(e)}
                                className="profile-form_btn">
                                {loading ? "please wait..." : "save"}
                            </button>
                        </form>
                    </div>
                </div>
            </main>
        </MainDash>
    );
}

export default Profile;
