import React from 'react';
// import {  } from "react-router-dom";
import { useSelector } from 'react-redux';
import { Redirect, NavLink } from "react-router-dom";

import Header from "./HeaderTop";
//icons
import {
    Store, Poll, Payment, ShoppingCart
} from "@material-ui/icons";

const MainDash = (props) => {
    const { user } = useSelector(state => state.user);
    let username;

    if (document.body.classList.contains("over-hidden")) {
        document.body.classList.remove("over-hidden");
    }

    if (user) {
        // if (user.name === undefined) return <Redirect to="/login" />
        username = user.name.split(" ")[0];
    }

    return (
        <section className="dashboard">
            <Header username={username} />
            <div className="dashboard-container">
                <nav className="dashboard-side_bar">
                    <div className="">
                        <h2 style={{ marginTop: "25px" }}
                            className="header-icon_txt">
                            ruby-stores
                        </h2>
                    </div>


                    <ul className="dashboard-sideNav">
                        <li className="dashboard-sideNav-item">
                            <NavLink className="dashboard-sideNav-link" activeclassname="dashboard-sideNav-link--active" to="/dashboard/home">
                                <span><Store className="dashboard-sideNav-logo" /></span>
                                <span>orders</span>
                            </NavLink>
                        </li>

                        <li className="dashboard-sideNav-item">
                            <NavLink className="dashboard-sideNav-link" to="/dashboard/cart" activeclassname="dashboard-sideNav-link--active">
                                <span><ShoppingCart className="dashboard-sideNav-logo" /></span>
                                <span>
                                    <span className="tx-block">
                                        my
                                    </span>
                                    cart
                                </span>
                            </NavLink>
                        </li>

                        <li className="dashboard-sideNav-item">
                            <NavLink className="dashboard-sideNav-link" activeclassname="dashboard-sideNav-link--active" to="/dashboard/profile">
                                <span><Poll className="dashboard-sideNav-logo" /></span>
                                <span>profile</span>
                            </NavLink>
                        </li>

                        <li className="dashboard-sideNav-item">
                            <NavLink className="dashboard-sideNav-link" activeclassname="dashboard-sideNav-link--active" to="/dashboard/setting">
                                <span><Payment className="dashboard-sideNav-logo" /></span>
                                <span>settings</span>
                            </NavLink>
                        </li>
                    </ul>
                </nav>
                <div className="dashboard-content">
                    {/* header component */}
                    <>
                        {props.children}
                    </>
                </div>
            </div>
        </section>
    );
}

export default MainDash;
