import React, { useState, useEffect, useCallback } from 'react';
import axios from "axios";
import { Star } from "@material-ui/icons";
import { Link, useParams, Redirect } from "react-router-dom";
import { addToCart } from "../actions/cart";
import { useDispatch, useSelector } from "react-redux";
import { Facebook, Twitter, Pinterest, LinkedIn, Reddit } from '@material-ui/icons';
// import {css, jsx} from "@emotion/react";

import HeaderTop from '../components/HeaderComponents/HeaderTop';
import CategoryProducts from "../components/productsComponents/CategoryProduct";
import Footer from '../components/Footer';
import Contact from '../components/contact';
import Searchpopup from "../components/searchPopup";
import NotiPopup from '../components/utils/NotiPopup';


const ProductDesc = () => {
    const dispatch = useDispatch();

    const { user, loggedIn } = useSelector(state => state.user);

    const [product, setProduct] = useState([]);
    const [count, setCount] = useState(1);
    const [value, setValue] = useState("");
    const [showContact, setShowContact] = useState(false);
    const [showSearch, setShowSearch] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");
    const [loading, setLoading] = useState(false);
    const [errMsg, setErrMsg] = useState("something went wrong!")

    let { id } = useParams();
    let star = [1, 2, 3, 4, 5];
    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_GET_STORE}/${id}`);
                setProduct(res.data.data.product);
            } catch (err) {
                console.log(`err is ${err}`);
            }
        }
        const timer = setTimeout(() => {
            fetchData();
        }, 500);

        return () => {
            clearTimeout(timer);
        }
    }, [id]);

    const reduceCount = () => {
        if (count > 1) setCount(count - 1);
    }
    const updateCount = () => {
        setCount(count + 1);
    }
    const getValue = useCallback((data) => {
        setValue(data);
    }, [value]);

    const onShowContact = () => setShowContact(!showContact);
    const onShowSearch = () => setShowSearch(!showSearch);

    const _addToCart = (e) => {
        e.preventDefault();
        setLoading(true);
        if(!loggedIn) {
            setErrMsg("login to gain access!");
        }
        const _data = {
            product: product._id,
            quantity: count
        }
        // console.log(_data);
        dispatch(addToCart(_data))
            .then(() => {
                setLoading(false);
                setColor("success");
                setContent("item added to cart");
                setPopup(true);
                // setTimeout(() => {
                //     return <Redirect to="/payment" />
                // }, 3000)
            })
            .catch(() => {
                setLoading(false);
                setColor("danger");
                setContent(errMsg);
                setPopup(true);
            });
    }
    if (popup) {
        setTimeout(() => {
            setPopup(false);
        }, 3000)
    }

    return (
        <div className="container">
            {showContact && <Contact onContact={() => onShowContact()} />}
            {showSearch && <Searchpopup onSearch={() => onShowSearch()} searchValue={value} />}
            {popup && <NotiPopup content={content} color={color} />}
            <HeaderTop showContact={() => onShowContact()} showSearch={() => onShowSearch()} parentCallback={getValue} />
            <div className="description">
                <div className="description-img">
                    <img src={`${process.env.REACT_APP_HOST_IMG}/${product.imageCover}`} alt={product.name} className={"description-photo"} />
                </div>
                <div className="description-box">
                    <h1 className="description-texthd">
                        {product.name}
                    </h1>
                    <div className={"description-conStars"}>
                        <span className={"product-stars description-stars"}>
                            {star.map(el => (
                                <Star className={`product-star--${product.ratingsAverage >= el ? "active" : "inactive"}`} />
                            ))}
                        </span>

                        <span className={"description-rating"}>&#123; customers review &#125;</span>
                    </div>
                    <div className={"description-price"}>${product.price}.00</div>
                    <p className="description-text1">
                        {product.description}
                    </p>
                    <div className="description-quantity">
                        <button className="add_button" onClick={() => updateCount()}>
                            +
                        </button>
                        <span>{count}</span>
                        <button className="sub_button" onClick={() => reduceCount()}>
                            -
                        </button>
                    </div>
                    <div className="description-buttons">
                        <Link className="description-buttons-1" to={loggedIn ? `/buy/${product._id}/${count}` : "/login"}>buy</Link>
                        <button className="description-buttons-2"
                            // to={"#"}
                            onClick={(e) => _addToCart(e)}
                        >
                            {loading ? "loading" : "Add to cart"}
                        </button>
                    </div>
                    <div className="description-details">
                        <h5 className="description-category">category: {product.category}</h5>
                        <h5 className="description-social">
                            <p>share this products:</p>
                            <span>
                                <Facebook className={"description-icon"} />
                            </span>
                            <span>
                                <Twitter className={"description-icon"} />
                            </span>
                            <span>
                                <Pinterest className={"description-icon"} />
                            </span>
                            <span>
                                <LinkedIn className={"description-icon"} />
                            </span>
                            <span>
                                <Reddit className={"description-icon"} />
                            </span>
                        </h5>
                    </div>
                </div>
            </div>
            <div className="description-related_products">
                <h2 className="category-text">Related Products </h2>
                <CategoryProducts id={product.category} />
            </div>
            <Footer />
        </div>
    )
}

export default ProductDesc;


