import React, { useEffect, useState } from 'react';
import axios from "axios";
import Whirligig from 'react-whirligig';

import CategoryCard from "./CategoryCard";

const CategoryProduct = ({ id }) => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [width, setWidth] = useState(5);

    // let Timer;
    let whirligig;
    const next = () => whirligig.next();
    const prev = () => whirligig.prev();

    const url = `${process.env.REACT_APP_GET_STORE}/category/${id}`;
    // console.log(url);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios.get(url);
                setProducts(res.data.data.products);
            } catch (err) {
                console.log("error", err);
            }
        }
        //fetchData();

        if (document.body.clientWidth >= 921 && document.body.clientWidth < 1110) {
            //phone size
            setWidth(4);
        } else if (document.body.clientWidth >= 626 && document.body.clientWidth < 920) {
            setWidth(3);
        } else if (document.body.clientWidth >= 436 && document.body.clientWidth < 625) {
            setWidth(2);
        } else if (document.body.clientWidth < 435) {
            setWidth(1);
        }

        const Timer = setTimeout(() => {
            fetchData();
        }, 1000);

        return () => {
            clearTimeout(Timer);
        }

    }, [id, width]);
    // console.log(products);
    const _products = products.slice(0, 4);

    return (
        <div className={"category"}>
            {React.Children.toArray(
                _products.map((el, i) => (
                    <CategoryCard name={el.name} price={el.price}
                        image={el.imageCover} id={id} ratings={el.ratingsAverage} link={ el._id}/>
                ))
            )}
            {/* <Whirligig visibleSlides={4} gutter={"1em"} preventScroll={false}
                ref={(_whirligigInstance) => { whirligig = _whirligigInstance }}
                style={{ padding: "2rem 0" }}>
                {React.Children.toArray(
                    products.map((el, i) => (
                        <CategoryCard name={el.name} price={el.price}
                            image={el.imageCover} id={id} ratings={el.ratingsAverage} />
                    ))
                )}
            </Whirligig> */}
        </div>
    );
}

export default CategoryProduct;