import React, { useState, useEffect } from 'react';
import escapeRegexp from "escape-string-regexp";
import axios from "axios";

import { Search } from "@material-ui/icons";
import SearchCard from '../components/searchCard';

const SearchPanel = () => {
    const [products, setProducts] = useState([]);
    const [searchValue, setSearchValue] = useState("");
    const [msg, setMsg] = useState("type a keyword to search...");
    let showingProduct;

    useEffect(() => {
        const fetchProduct = async () => {
            try {
                const res = await axios.get(process.env.REACT_APP_GET_STORE);
                setProducts(res.data.data.products);
            } catch (err) {
                console.log(`error is ${err.message}`)
            }
        }
        const timer = setTimeout(() => {
            fetchProduct();
        }, 500);
        return () => {
            clearTimeout(timer);
        }
    }, []);
    const onSubmit = (e) => {
        e.preventDefault();
    };

    if (searchValue.length >= 3) {
        const match = new RegExp(escapeRegexp(searchValue), 'i');
        showingProduct = products.filter((product) => match.test(product.name));
    } else {
        showingProduct = [];
    }

    return (
        <section className="searchpanel">
            <header className="searchpanel-form">
                <form className={"searchpanel-form"}
                    onSubmit={(e) => onSubmit(e)}
                >
                    <button className={"searchpanel-form_button"}>
                        <Search className={"searchpanel-form_icon"} />
                    </button>
                    <input
                        type="search"
                        className={"searchpanel-form_input"}
                        placeholder={`search for gadgets`}
                        // ref={searchRef}
                        onChange={(e) => setSearchValue(e.target.value)}
                        onFocus={true}
                    />
                </form>
            </header>
            <main className="searchpanel_block">

                {showingProduct.length !== 0 ? <>
                    {showingProduct.map((prod, i) => (
                        <>
                            <SearchCard
                                name={prod.name}
                                photo={prod.imageCover}
                                id={prod.id}
                                price={prod.price}
                                category={prod.category}
                            />
                        </>
                    ))}
                </> : <>
                    {/* not found */}
                    <h2 className="searchpanel_notText">{msg}</h2>
                </>}
            </main>
        </section>
    );
}

export default SearchPanel;
