import React, { useState, useEffect } from 'react';
import { useParams } from "react-router-dom";
import axios from "axios";

//icons
import {
    Lock, Store, Poll, Payment, ShoppingCart
} from "@material-ui/icons";
import { FaPaypal } from "react-icons/fa";
import { BsCreditCardFill } from "react-icons/bs";

const Buy = () => {
    const { id, quan } = useParams();
    const [credit, setCredit] = useState(true);
    const [product, setProduct] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_GET_STORE}/${id}`);
                setProduct(res.data.data.product);
            } catch (err) {
                console.log(`err is ${err}`);
            }
        }
        const timer = setTimeout(() => {
            fetchData();
        }, 500);

        return () => {
            clearTimeout(timer);
        }
    }, [id]);

    return (
        <section className="payment-section">
            <div className="payment-inner">
                <header className="payment-header">
                    <div>&nbsp;</div>
                    <span>payment method</span>
                    <div className="payment-header-secure">
                        <Lock className="payment-header-secure-icon" />
                        <span>secure</span>
                    </div>
                </header>
                <div className="payment-info">
                    <div className="payment-method m-0">
                        <h4>payment method</h4>
                        <form className="payment-form">
                            <div className="payment-option">
                                <input type="radio"
                                    name={"payment"}
                                    defaultChecked={credit}
                                    checked={credit} />
                                <span>
                                    <BsCreditCardFill />
                                </span>
                                <span>credit card</span>
                            </div>

                            <div className="payment-option">
                                <input type="radio" name={"payment"} checked={false} />
                                <span>
                                    <FaPaypal />
                                </span>
                                <span>paypal</span>
                                <span className="not_available">not yet available</span>
                            </div>

                            <div className="payment-delivery_type">
                                <h4 className="payment-h3_text">delivery type</h4>
                                <div className="payment-option">
                                    <input type="radio" name={"delivery"} defaultChecked={credit} />
                                    <span>
                                        <Store />
                                    </span>
                                    <span>home delivery</span>
                                </div>

                                <div className="payment-option">
                                    <input type="radio" name={"delivery"} />
                                    <span>
                                        <ShoppingCart />
                                    </span>
                                    <span>pickup</span>
                                </div>
                            </div>

                            <h3 className="payment-h3_text">sum of ${product.price * quan} to be paid for</h3>
                            <div className="payment-product">
                                <img src={`${process.env.REACT_APP_HOST_IMG}/${product.imageCover}`} alt={product.name} className="payment-product_image" />
                                <h4 className="payment-product_text">
                                    <span>{product.name}</span>
                                    <span>quantity:&nbsp; {quan}</span>
                                </h4>
                            </div>

                            <div className="payment-card">
                                <div className="payment-group">
                                    <label className="payment-label" htmlFor="name">name on card:</label>
                                    <input type="text"
                                        id="name"
                                        placeholder='john doe'
                                        className="payment-input" />
                                </div>

                                <div className="payment-group">
                                    <label className="payment-label" htmlFor="card">card number:</label>
                                    <input type="text"
                                        id="card"
                                        placeholder='**** **** **** 1234'
                                        className="payment-input" />
                                </div>
                            </div>

                            <div className="payment-flex">
                                <div className="payment-group expiry">
                                    <label className="payment-label" htmlFor="">expiry date:</label>
                                    <div className="payment-flex">
                                        <input type="number" min={"1"} max={"12"} placeholder={"05"} className="payment-input-1" />
                                        <span className="payment-slash">/</span>
                                        <input type="number" min={"1"} max={"50"} placeholder={"27"} className="payment-input-1" />
                                    </div>
                                </div>
                                <div className="payment-group cvv">
                                    <label className="payment-label" htmlFor="">cvv:</label>
                                    <input type="number" placeholder='156' className="payment-input-2" />
                                </div>

                            </div>
                            <button className="payment_btn">send</button>
                        </form>
                    </div>
                </div>

            </div>
        </section>
    );
}

export default Buy;
