import React from 'react';
import { Link } from "react-router-dom";

const SearchCard = ({ id, photo, name, category, price }) => {
    return (
        <Link to={`/productDesc/${id}`} className="search-link">
            <li key={id} className={"search-list-item"}>
                <div className="search-avatar">
                    <img src={`${process.env.REACT_APP_HOST_IMG}/${photo}`} alt={name} className="search-photo" />
                </div>
                <div className={"search-details"}>
                    <p>{name}</p>
                    <p>{category}</p>
                </div>
                <span className={"search-price"}>
                    ${price}
                </span>
            </li>
        </Link>
    );
}

export default SearchCard;
