import axios from "axios";
import {
    ADD_TO_CART_FAILURE, ADD_TO_CART_SUCCESS, REMOVE_FROM_CART_SUCCESS, REMOVE_FROM_CART_FAILURE
} from "./types";

export const handleRequest = (data, req, url, successType, failureType) =>
    async (dispatch) => {
        let token = JSON.parse(localStorage.getItem("token"));
        try {
            let res;
            if (req === "delete")
                res = await axios[req](url, {
                    headers: {
                        "Authorization": `bearer ${token}`
                    }
                });

            if (req === "post") {
                res = await axios[req](url, data, {
                    headers: {
                        "Authorization": `bearer ${token}`
                    }
                });
            }

            dispatch({
                type: successType,
                message: "success",
                payload: res.data
            });
            return Promise.resolve();
        } catch (err) {
            const { error } = err.response.data;
            dispatch({
                type: failureType,
                message: error
            });
            return Promise.reject();
        }
    }

export const addToCart = (data) =>
    handleRequest(data, "post", process.env.REACT_APP_GET_CART, ADD_TO_CART_SUCCESS, ADD_TO_CART_FAILURE);

export const removeItem = (id) =>
    handleRequest("", "delete", `${process.env.REACT_APP_GET_CART}/${id}`, REMOVE_FROM_CART_SUCCESS, REMOVE_FROM_CART_FAILURE);

