import axios from "axios";
import {
    LOGIN_SUCCESS, LOGIN_FAILURE, SIGNUP_FAILURE, SIGNUP_SUCCESS, LOGOUT,
    UPDATE_PROFILE_SUCCESS, UPDATE_PROFILE_FAILURE
} from "./types";

export const signUp = (data) =>
    async (dispatch) => {
        // const dataObj = JSON.stringify(data);
        // console.log("data coming from action " + dataObj);
        try {
            const res = await axios.post(process.env.REACT_APP_SignUpUserApi, data);
            // console.log(res.data);
            dispatch({
                type: SIGNUP_SUCCESS,
                message: "success",
                payload: res.data
            });
            return Promise.resolve();
        } catch (err) {
            const { error } = err.response.data;
            let message = "something went wrong!";
            if (error.code === 11000) message = "email already exist!"
            dispatch({
                type: SIGNUP_FAILURE,
                message
            });
            return Promise.reject();
        }
    }

export const login = (data) =>
    async (dispatch) => {
        // const dataObj = JSON.stringify(data);
        // console.log(process.env.REACT_APP_loginUserApi);
        try {
            const res = await axios.post(process.env.REACT_APP_loginUserApi, data);
            if (res.data.token) {
                // console.log("here from action");
                localStorage.setItem("token", JSON.stringify(res.data.token));
                // console.log(res.data.token);
            }
            dispatch({
                type: LOGIN_SUCCESS,
                message: "Login successful!",
                payload: res.data
            });
            return Promise.resolve();
        } catch (err) {
            const { error } = err.response.data;
            // console.log(error);
            let message;
            if (error) {
                if (error.statusCode === 401) message = "incorrect email or password!";
            } else {
                message = "server not reachable!";
            }

            dispatch({
                type: LOGIN_FAILURE,
                message
            });
            return Promise.reject();
        }
    }

export const updateMe = (data) =>
    async (dispatch) => {
        // console.log(token);
        let token = JSON.parse(localStorage.getItem("token"));
        // const dataObj = JSON.stringify(data);
        // console.log(process.env.REACT_APP_UPDATE_PROFILE);
        try {
            const res = await axios.patch(process.env.REACT_APP_UPDATE_ME, data, {
                headers: {
                    "Authorization": `bearer ${token}`
                }
            });
            dispatch({
                type: UPDATE_PROFILE_SUCCESS,
                message: "Login successful!",
                payload: res.data
            });
            return Promise.resolve();
        } catch (err) {
            const { error } = err.response.data;
            // console.log(error);
            let message;
            if (error) {
                if (error.statusCode === 401) message = "incorrect email or password!";
            } else {
                message = "server not reachable!";
            }

            dispatch({
                type: UPDATE_PROFILE_FAILURE,
                message
            });
            return Promise.reject();
        }
    }
