import React from 'react';
import { Star } from "@material-ui/icons";
import { Link } from "react-router-dom";

const CategoryCard = ({ name, price, image, id, ratings, link }) => {
    let star = [1, 2, 3, 4, 5];
    return (
        <Link className={"product-link category-grid"} to={`/productDesc/${link}`}>
            <div className="category-img">
                <img src={`${process.env.REACT_APP_HOST_IMG}/${image}`} alt={name} className={"category-photo"} />
            </div>
            <div className="category-box">
                <div className="category-det">
                    <span className="category-id">{id}</span>
                    <span className={"category-stars"}>
                        {star.map(el => (
                            <Star className={`product-star--${ratings >= el ? "active" : "inactive"}`} />
                        ))}
                    </span>
                </div>
                <h4 className="category-name">{name}</h4>
                <h4 className="category-price">${price}.00</h4>
            </div>
        </Link>
    );
}

export default CategoryCard;
