import React, { useState } from 'react';
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
// import { login } from "../../actions/auth";
import { Link } from "react-router-dom";

import NotiPopup from '../components/utils/NotiPopup';
//icons
import { RiErrorWarningLine } from "react-icons/ri";

const Contact = () => {

    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const [isDisabled, setIsDisabled] = useState(true);
    const [nameMsg, setNameMsg] = useState("");
    const [emailError, setEmailError] = useState(false);
    const [loading, setLoading] = useState(false);
    const [popup, setPopup] = useState(false);
    const [color, setColor] = useState("");
    const [content, setContent] = useState("");


    const onEmailChange = (e) => {
        setEmail(e.target.value);
        const pattern =
            /[a-zA-Z0-9]+[\.]?([a-zA-Z0-9]+)?[\@][a-z]{3,9}[\.][a-z]{2,5}/g;
        if (pattern.test(e.target.value)) {
            e.persist();
            setEmail(e.target.value);
            setEmailError(false);
            setIsDisabled(false);
        } else {
            setEmailError(true);
        }
    };

    const onSubmit = async (e) => {
        e.preventDefault();
        if (emailError === false) {
            let data = {
                email,
                message
            }
            try {
                const res = await axios.post(process.env.REACT_APP_CONTACT, data);
                if (res.data.status === "success") {
                    setPopup(true);
                    setContent("Thank you for your feedback!");
                    setColor("success");
                }
                // console.log(res.data);
            } catch (err) {
                setContent("oops something went wrong");
                setColor("danger");
                setPopup(true);
            }

        }
    }

    if (popup)
        setTimeout(() => {
            setPopup(false);
        }, 3000);
    return (
        <main className="contact contact-grid"
            style={{ zIndex: "2" }}
            draggable={true}
        >
            {popup && <NotiPopup content={content} color={color} />}
            <div className="contact-box fx" style={{ zIndex: "0", height: "60vh" }}>
                <h4 className="contact_form-text">Contact Us</h4>
                {/* <a href="/" className="contact-close">&times;</a> */}
                <Link to="/" className="contact-close">&times;</Link>
            </div>
            {/* <p>send us your message and we will get back to you</p> */}
            <form action="" className="contact-form" onSubmit={(e) => onSubmit(e)}
                style={{ zIndex: "20" }}
            >
                <p>send us your message and we will get back to you</p>
                <div className="contact-div">
                    <div className="contact-form_group">
                        <input type="email"
                            // style={{ padding: "0.6rem 13rem 0.6rem 0.7rem" }}
                            onChange={(e) => onEmailChange(e)}
                            className={`${emailError && "outline-red"} contact-form_input `}
                            placeholder="enter your email address"
                            id="email"
                            name={"email"}
                            required={true} />
                        <label htmlFor="email" className="contact-form_label">email address</label>
                        {
                            emailError ? (<p className="contact-warningText">
                                <span>
                                    <RiErrorWarningLine className="contact-warningLogo" />
                                </span>
                                please input a valid email address
                            </p>) : ""
                        }
                    </div>
                    <div className="contact-form_group">
                        <div className="contact-flex">
                            <textarea
                                onChange={(e) => setMessage(e.target.value)}
                                className={`contact-form_input contact-form_input-1`}
                                placeholder="Message"
                                id="message"
                                name={"message"}
                                style={{ marginBottom: "0.6rem" }}
                                required={true} />
                        </div>
                        {/* <label htmlFor="message" className="contact-form_label">message</label> */}
                    </div>

                    <div className="contact-form_buttons">
                        <button
                            className={`${isDisabled && "contact-disabled"} contact-form_btn`}
                            type={"submit"}
                            disabled={isDisabled}
                            onClick={(e) => onSubmit(e)}
                        >
                            {loading ? "please wait" : "Send message"}
                        </button>
                    </div>
                </div>
            </form>
        </main>
    );
}

export default Contact;
