import React, { useState } from 'react';
import { Link, NavLink } from "react-router-dom";
import { useSelector } from "react-redux";

const Respanel = ({ history, onClose }) => {
    const [openPanel, setOpenPanel] = useState(false);
    const { loggedIn, user } = useSelector((state) => state.user);

    const onLogout = () => {
        // dispatch(logout);
        localStorage.removeItem("token");
        // console.log("here");
        // // history.push("/");
        // onClose();
        // window.location.reload();
    }

    return (
        <div className="navigation-box">
            <nav className="navigation__nav">
                <ul className="navigation__list">
                    <li className="navigation__items">
                        <Link to="/" className="navigation__link" onClick={onClose}>Home</Link>
                    </li>
                    <li className="navigation__items">
                        <Link to="/" className="navigation__link" onClick={onClose}>Products</Link>
                    </li>
                    <li className="navigation__items">
                        <Link to="/" className="navigation__link" onClick={onClose}>Deals of the day</Link>
                    </li>
                    <li className="navigation__items">
                        <Link to="/contact" className="navigation__link" >Contact us</Link>
                    </li>
                    {
                        loggedIn ? <>
                            <li className="navigation__items">
                                <Link to="/dashboard/home" className="navigation__link navigation__btn-2" >dashboard</Link>
                            </li>
                            {/* <li className="navigation__items">
                                <Link to="#" className="navigation__link navigation__btn-1" onClick={onLogout}>Log out</Link>
                            </li> */}
                        </> : <>
                            <li className="navigation__items">
                                <Link to="/signup" className="navigation__link navigation__btn-1" >Create account</Link>
                            </li>
                            <li className="navigation__items">
                                <Link to="/login" className="navigation__link navigation__btn-2" >Sign in</Link>
                            </li>
                        </>
                    }

                </ul>
            </nav>
        </div>
    );
}

export default Respanel;
