import React, { useState, useEffect } from 'react';

const Paymentbox = ({ photo, quan, name, category, price, id, totalCallback }) => {
    const [count, setCount] = useState(quan);
    const [nPrice, setPrice] = useState(price);
    let total = (count * nPrice) * 1;
    const [totalPri, setTotalPri] = useState(total);


    const reduceCount = () => {
        if (count > 1) setCount(count - 1);
        setTotalPri(count * nPrice);
        // total = count * price;
        // console.log(total);
        // setTotalPri(total);
        // totalCallback(totalPri);
    }
    const updateCount = () => {
        setCount(count + 1);
        setTotalPri(count * nPrice);
        // total = count * price;
        // setTotalPri(total);
        // totalCallback(totalPri);
    }

    useEffect(() => {
        setTotalPri(total);
        console.log(count, totalPri, price);
        const timer = setTimeout(() => {
            totalCallback(totalPri);
        }, 500);
        return () => {
            clearTimeout(timer);
        }
    }, [totalPri, count]);

    return (
        <li className="payment-list" key={id}>
            <div className="img">
                <figure className="figure">
                    <img src={`${process.env.REACT_APP_HOST_IMG}/${photo}`} alt="product image" className='image' />
                </figure>
                <div className="details">
                    <span>{name}</span>
                    <span>
                        {category}
                    </span>
                </div>
            </div>
            <div className="quantity">
                <button className="add_button" onClick={updateCount}>
                    +
                </button>
                <span>{count}</span>
                <button className="sub_button" onClick={reduceCount}>
                    -
                </button>
            </div>
            <div className="price">
                <span>${price}</span>
                <span
                    className="times"
                >
                    &times;
                </span>
            </div>
        </li>
    );
}

export default Paymentbox;
